body {
  background-color: #f0f1ff;
  margin: 0;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

header {
  width: 100vw;
  height: 60px;
  background-color: rgba(4, 0, 221, .337);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.search-form {
  justify-content: center;
  padding-top: 10px;
  display: flex;
}

input {
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 8px;
  outline: rgba(0, 0, 0, 0);
  margin: 0;
  padding: 10px 15px 10px 20px;
  font-size: 14px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

input:focus, input:hover {
  border-color: #188ce8;
}

.submit-button {
  cursor: pointer;
  background-color: #f0f1ff;
  border-style: none;
  border-radius: 8px;
  margin-left: 10px;
  padding: 10px 40px;
  font-size: 14px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.submit-button:hover, .submit-button:focus {
  background-color: #188ce8;
}

.load-more {
  cursor: pointer;
  background-color: rgba(4, 0, 221, .337);
  border-style: none;
  border-radius: 8px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 40px;
  font-size: 14px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.load-more:hover, .load-more:focus {
  background-color: #188ce8;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.gallery {
  justify-content: center;
  align-items: inherit;
  flex-wrap: wrap;
  gap: 15px;
  margin-top: 40px;
  margin-bottom: 40px;
  display: flex;
}

.photo-card {
  max-width: 370px;
  border-radius: 8px;
  flex-basis: calc(33.3333% - 20px);
  overflow: hidden;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.info {
  margin-left: 20px;
}

